@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
  --rosa-oscuro: #ed6e8d;
  --rosa-claro: #f2a3b8;
  --rosa-search: #e0b9c3;
  --gris: #dcdcdc;

  --color-claro-buscador: rgba(255, 255, 255, 0.5);
  --color-fuente-buscador: white;
  --color-bordes: rgba(0, 0, 0, 0.1);
}

:root {
  --fuente-texto: "Roboto", sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

* {
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: inherit;
}


header {
  height: 60px;
  background-color: var(--rosa-claro);
  display: flex;
  align-items: center;
  justify-content: center;
}

#contenedorBuscadorHeader {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid white; 
}

#buscador{
  flex-grow: 1; 
  background-color: var(--rosa-claro);
  outline-style: none;
  font-size: 16px;
  font-weight: bold;
  border: none;
  color: white;
}

#IconoBuscador{
  color: white;
}

#buscador::-webkit-input-placeholder {
  color: var(--color-claro-buscador);
}

#buscador:-ms-input-placeholder {
  color: var(--color-claro-buscador);
}

#buscador::placeholder {
  color: var(--color-claro-buscador);
}
.H1{
    text-align: center;
    font-family: var(--fuente-texto);
    border-bottom: 2px solid var(--color-bordes);
  }

.Inicio{
  text-decoration: none;
  color: black;
}  
