footer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; 
  background-color: var(--rosa-claro);
  position: fixed;
  bottom: 0vh;
  width: 100%;
  height: 60px; 
}

.logos {
display: flex;
justify-content: center;
}

.logos{
  color: white;
}

.c{
text-decoration: none;
color: white;

}
.ContenedorLinkFooter{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 1rem;
}

.IconoLinkFooter{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
    font-size: 25px;
}

.TextoLinkFooter {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--fuente-texto);
}
.H4{
    font-family: var(--fuente-texto);
    border-bottom: 2px solid var(--color-bordes);
}
.contenedorTarjetas {
    margin: 0px;
    display: grid;
    justify-content: center;
    justify-items: center;
    
    width: 100%;
}

@media (min-width: 600px)  {
    .contenedorTarjetas {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 2px 2px;
        gap: 2px 2px;
        
    }
}
@media (min-width: 1024px)  {
    .contenedorTarjetas {
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 2px 2px;
        gap: 2px 2px;
        
    }

}
.cuerpoTarjeta{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-style: solid;
    border-top: none;
    border-color: var(--color-bordes);
}

.fotoDePerfil{
    border-radius: 50%;
    height: 40px;
    width: 40px;
    margin-left: 10px;
}

.imagenP{
    width: 100%;
}

.TituloDelHaiku{
    font-family: var(--fuente-texto);
    text-align: center;
    margin-left: 10px;
}

.icono{
    color: grey;
    font-size: 25px;
    margin-right: 10px;
}

.fotoYTituloH{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Haiku {
    position: absolute;  
    background-color: rgba(0, 0, 0, 0.4);
    padding-block: 150px;
    color: white;
}

.imagenYHaiku{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    
}


.ventana{
    width: 200px;
}

.icons {
    display: flex;
    justify-content: center;
    color:var(--rosa-claro);
    font-size: 32px;
}

.iconFace {
    color: white;
}

.texto{
    text-align: center;
    margin: 10px;
    padding: 10px;
}
.contenedorDeContacto {
    display: flex;
    justify-content: center;
    margin: 1rem;
}
